  } */
&__image{
    align-self: center;
    justify-self: center;
   
    &:hover {
        transform: scale(1.05, 1.05);
        transition: transform 2s;
      }
      &:not(:hover) {
        transition: transform 2s;
      }
}
.detailtextbox{
   text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-self: center;
    gap: 20px;
   
}
&__name{
  font-size: 30px;  
